pl.game.component('catch', function () {

	this.behavior('select', function (_target) {
		var $target, id;

		$target = $(_target);
		id = $target.id() || $target.index();

		if (this.shouldSelect($target) !== false) {
			return {
				message: id,
				behaviorTarget: $target
			};
		}

		return false;
	});

	this.shouldSelect = function (_target) {
		return true;
	};

	this.deselectAll = function() {
		var items = this.find('li');
		this.deselect(items);
		this.unhighlight(items);
	};

	this.start = function() {
		this.eachFrame(this.onEachFrame);
	};

	this.stop = function () {
		this.eachFrame(this.onEachFrame, false);
	};

	this.onEachFrame = function () {
		var opening = this.getOpening();

		this.$items.each(function (i, item) {
			var rect = this.getItemRect(item);

			if(opening.top < rect.bottom && opening.top > rect.top) {
				if((rect.left > opening.left && rect.left < opening.right) || (rect.right > opening.left && rect.right < opening.right)) {
					this.select(item);
				}
			}

		}.bind(this));
	};

	this.getOpening = function() {
		return this.$bucket[0].getBoundingClientRect();
	};

	this.getItemRect = function(item) {
		var rect = item.getBoundingClientRect();

		return {
			top: rect.top + (rect.bottom-rect.top)/4,
			bottom: rect.bottom - (rect.bottom-rect.top)/4,
			left: rect.left + (rect.right-rect.left)/4,
			right: rect.right - (rect.right-rect.left)/4,
		};
	};

	this.on('ready', function(_e) {
		var $bucket;

		if(!this.is(_e.target)) return;

		this.$items = this.find('.items li');
		this.$bucket = this.find('[pl-id=bucket]');
		this.catch = false;

		$('.catch-component').on('mousemove touchstart touchmove', function(e){
			e.preventDefault();
			this.$bucket.css({left: e.originalEvent.pageX / this.game.zoom - 72});
		}.bind(this));
	});

});



